@import url(https://fonts.googleapis.com/css?family=Oswald:200,300,regular,500,600,700);

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Oswald;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: url('./assets/documents-of-victory.jpg') 0 0 no-repeat;
  background-size: cover;
  letter-spacing: 1px;
}

.App {
  width: 500px;
  border-radius: 5px;
  padding: 40px;
  background-color: #fff;
  position: relative;

  .result {
    text-align: center;

    button {
      font-weight: 400;
      font-family: 'Nunito', sans-serif;
      border-radius: 30px;
      border: 0;
      padding: 15px 30px;
      font-size: 20px;
      background-color: rgb(24, 187, 54);
      color: #fff;
      margin-top: 20px;
      cursor: pointer;
    }

    h2 {
      margin-bottom: 0;
    }

    img {
      width: 150px;
    }
  }

  .progress {
    height: 10px;
    border-radius: 30px;
    background-color: rgb(232, 232, 232);
    margin-bottom: 25px;

    &__inner {
      height: 100%;
      border-radius: 30px;
      width: 80%;
      transition: all 0.3s ease-in-out;
      background: rgb(24, 187, 54);
    }

  }



  h1 {
    margin: 0;
    font-weight: 400;
  }

  ul {
    list-style: none;
    padding: 0;

    li {
      padding: 15px;
      border: 2px solid rgba(0, 0, 0, 0.1);
      border-radius: 5px;
      margin-bottom: 10px;
      cursor: pointer;
      transition: all 0.1s ease-in-out;
      font-weight: 300;

      &:hover {
        border: 2px solid rgba(0, 0, 0, 0.3);
      }
    }
  }
}